import React, {useEffect, useState} from 'react'
import LinkForm from './LinkForm'
import {db} from '../../firebase'
import './Tablas.estilos.css'
import {toast} from 'react-toastify'

const Links = ({handleLogout}) => {

    const [links, setLinks] = useState([]);
    const[currentId, setCurrentId] = useState("");
    const addOrEditLink = async (LinkObjets) => {
        if (currentId === ""){
            await db.collection("links").doc().set(LinkObjets)
            toast('Nueva entrada',{ //Para el coso verde al a√±adir
            type: 'success'
        });
        }
        else {
            await db.collection('links').doc(currentId).update(LinkObjets)
            toast('Actualizado',{ //Para el coso verde al a√±adir
                type: 'info'
            });
            setCurrentId('')
        }
    };
    const onDeleteLink = async (id) => {
        if (window.confirm("estas seguro de querer eliminar este usuario?")){
            await db.collection('links').doc(id).delete();
            toast('Usuario eliminado',{ //Para el coso verde al a√±adir
                type: "error",
                autoClose: 2000,
            });
        }
    }
    const getLinks = async() =>{
        db.collection("links")
        .onSnapshot((querySnapshot) => {
            const docs = [];
            querySnapshot.forEach((doc) => {
                docs.push({...doc.data(), id:doc.id});
                console.log(docs)
        });
        setLinks(docs);
    });
}

    useEffect( () => {
        getLinks();
    }, []);
    return(
    <div>
        <button onClick={handleLogout}>Cerrar sesi√≥n</button>
        <div>
            <LinkForm {...{addOrEditLink, currentId, links}}/>
        </div>
        <div className="acomodaTabla">
        <table className="tablaResultados">
                <thead>
                    <tr>
                        <th className="chikito2">Tipo</th>
                        <th>Documento</th>
                        <th>Nombre completo</th>
                        <th className="chikito2">Correo</th>
                        <th>Telefono</th>
                        <th>Direcci√≥n</th>
                        <th>Sintomatolog√≠a</th>
                        <th className="peque√±o">Dosis</th>
                        <th className="peque√±o">Laboratorio</th>
                        <th>Opciones</th>
                    </tr>
                    </thead>
                    <tbody id="tabla">
        {links.map(link => (
                    <tr>
                        <td>{link.tipoDocumentos}</td>
                        <td>{link.documento}</td>
                        <td>{link.nombre}</td>
                        <td className="chikito">{link.correo}</td>
                        <td>{link.telefono}</td>
                        <td>{link.direccion}</td>
                        <td>{link.sintomas}</td>
                        <td className="peque√±o">{link.dosis}</td>
                        <td className="peque√±o">{link.laboratorio}</td>
                        <td>
                            <button className="botonEditar" onClick={() => setCurrentId(link.id)}>
                                Lapiz</button>
                            <button className="botonEliminar" onClick={() => onDeleteLink(link.id)}>X</button>
                        </td>
                    </tr>
            ))}
            </tbody>
            </table>
        </div>
    </div>
    )
};
export default Links;